.nav {
  color: $haze;
  display: flex;
  justify-content: space-between;
  height: 100%;
  @include content;
  &_active {
    background-color: rgba($light, 0.05);
    border-radius: 0.25rem; }
  &, &_body {}
  &_icon {
    width: 0.7rem;
    margin-left: 0.33rem;
    transition: transform 0.3s var(--ease);
    transform-origin: 50% 50%; }
  &_body {
    display: flex;
    flex: 1; }
  &_header {
    padding: 0 1.5rem;
    // margin-top: 5%
    position: fixed;
    width: 100%;
    z-index: 99;
    left: 0;
    height: 10%;
    @media screen and(max-width: 690px) {
      margin-top: 0%;
      background-color: $bg; } }
  &_item {
    padding: 0.5rem 1rem;
    display: inline-flex;
    align-items: center; }
  &_sub {
    width: 100%;
    left: 0;
    position: absolute;
    z-index: 20;
    border-radius: 0 0 0.5rem 0.5rem;
    top: 3rem;
    transition: height 0.3s ease-in-out;
    height: 0;
    overflow: hidden;
    padding: 0.96rem 0 0;
    background: transparent; }

  &_parent {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0.25rem 0;
    border-radius: 0.5rem; }

  &_open + &_sub &_child {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    z-index: 3;
    & + a {
      @media screen and (min-width: $mobile-menu-breakpoint) {
        padding-top: 0; } }
    &:not(:first-child) {
      position: relative; }
    &:first-child::before {
      position: absolute;
      content: "";
      @extend %upcaret;
      border-bottom: 0.5rem solid var(--translucent-light);
      z-index: 2; } }
  &_open + &_sub {
    height: initial;
    z-index: 999;
    overflow: initial;
    border-radius: 0.5rem;
    padding-bottom: 0.5rem;
    @media screen and (min-width: $mobile-menu-breakpoint) {
      color: var(--text); }
    &::before, &::after {
      content: "";
      position: absolute; }
    &::before {
      @extend %upcaret;
      z-index: 1;
      @media screen and (min-width: $mobile-menu-breakpoint) {
        border-bottom: 0.5rem solid var(--choice-bg); } }
    &::after {
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: var(--translucent-light);
      border-radius: 0.5rem;
      box-shadow: 0 1rem 3rem rgba(0,0,0,0.25); }
    @media screen and (min-width: $mobile-menu-breakpoint) {
      background: var(--choice-bg);
      width: auto;
      white-space: nowrap; } }
  &_open &_icon {
    transform: rotate(180deg); }

  &_sub &_item {
    &:not(.nav_child) {
      position: relative;
      z-index: 5;
      display: block;
      padding-top: 0.75rem;
      padding-bottom: 0.75rem;
      transition: background 0.3s ease-out;
      margin: 0; } }

  &_brand {
    img {
      max-width: 15vw;
      margin: 0; }
    @media screen and (min-width: $mobile-menu-breakpoint) {
      padding-left: 0;
      // margin-left: -16rem
      // transform: translateX(50rem)
      img {
        background: transparent !important;
        height: 100%; } } }

  &_center {
    display: none;
    @media screen and (min-width: $mobile-menu-breakpoint) {
      display: flex;
      flex: 1;
      align-items: center;
      justify-content: center;
      + .follow {
        flex: initial; } } }

  &_hide {
    @media screen and (min-width: $mobile-menu-breakpoint) {
      display: none; } }
  &_close {
    display: none; } }

